<script>
	import {default as music} from "./data/music.js";

	let selectedSong = null, player = null;
</script>

{#if selectedSong !== null}
	<img
		height="400"
		src={music[selectedSong]?.bg}
		alt="'Background'"
	>
{/if}
{#if selectedSong !== null}
	<h1>{music[selectedSong].name}</h1>
	<h2>"{music[selectedSong].title}"</h2>
{/if}
<audio bind:this={player} controls={true} src={music[selectedSong]?.url}></audio>

<br />

<table>
	<thead>
		<tr>
			<th></th>
			<th>Name</th>
			<th>Title</th>
			<th>Internal name</th>
			<th>Category</th>
		</tr>
	</thead>
	{#each music as obj, index}
		<tr class:selected={selectedSong === index}>
			<td>
				<button
					on:click={() => {
						selectedSong = index;
						player.load();
						player.autoplay = true;
					}}
				>
					>
				</button>
			</td>
			<td>{obj.name}</td>
			<td>{obj.title}</td>
			<td>{obj.internalName}</td>
			<td>{obj.category}</td>
		</tr>
	{/each}
</table>

<style>
	audio {
		display: block;
		width: 100%;
	}
	.selected {
		font-weight: bold;
	}
</style>